@font-face {
  font-family: "Raleway Regular";
  src: url("/src/assets/fonts/raleway/raleway-regular.ttf") format("truetype"); }

@font-face {
  font-family: "Raleway Bold";
  src: url("/src/assets/fonts/raleway/raleway-bold.ttf") format("truetype"); }

.l-about__area {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 20px 0; }

@media screen and (max-width: 768px) {
  .l-about__area {
    flex-direction: column; }
    .l-about__area .l-perfil__area {
      order: 0;
      align-self: center; }
    .l-about__area .c-about__me {
      order: 1; } }

body {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #202835;
  font-family: 'Raleway Regular', sans-serif;
  color: #FFFFFF;
  font-weight: 700; }

.l-card__hobbies {
  align-items: center;
  background-color: #293342;
  border-radius: 5px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  height: 90px;
  justify-content: center;
  padding: 4px;
  width: 300px; }

.l-card__project {
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.45);
  background-color: #293342;
  border-radius: 6px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  height: 265px;
  max-width: 360px;
  min-width: 310px;
  overflow: hidden;
  position: relative;
  width: 100%; }

.l-card__project__body {
  background-color: #293342;
  bottom: 0;
  box-sizing: border-box;
  cursor: default;
  display: flex;
  flex-direction: column;
  height: 70px;
  justify-self: end;
  overflow: hidden;
  padding: 10px;
  position: absolute;
  transition: 0.4s;
  width: 100%; }
  .l-card__project__body:hover {
    animation: over 0.6s;
    height: 65%;
    transition: all 0.8s; }
    .l-card__project__body:hover .c-card__project__show__description {
      transform: translateX(-50%) rotate(180deg);
      transition: all 0.8s; }

@keyframes over {
  0% {
    overflow: hidden; }
  100% {
    overflow: visible; } }

.l-card__project__button {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 10px;
  right: 10px; }

.l-carrossel {
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.45);
  align-items: center;
  background-color: #293342;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  height: 90px;
  overflow: hidden;
  width: 300px; }

.l-carrossel__container {
  display: flex;
  flex-direction: row;
  transition: transform 0.4s ease-in-out; }

.l-container {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  max-width: 1136px;
  padding: 0 20px;
  width: 100%; }

.l-navbar {
  align-items: center;
  background-color: #202835;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  height: 5rem;
  justify-content: space-between;
  max-width: 1136px;
  width: 100%; }

.l-perfil__area {
  display: flex;
  align-items: center;
  max-width: 250px;
  width: 100%;
  justify-content: center; }

.l-projects__container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(310px, auto));
  row-gap: 16px;
  column-gap: 10px; }

.l-skill__hobbies {
  display: flex; }

@media screen and (min-width: 0) {
  .l-skill__hobbies {
    flex-direction: column;
    align-items: center; } }

@media screen and (min-width: 690px) {
  .l-skill__hobbies {
    justify-content: space-between;
    flex-direction: row; } }

@media screen and (min-width: 768px) {
  .l-skill__hobbies {
    align-self: center;
    flex-direction: column; } }

.c-navbar__item {
  padding: 15px;
  box-sizing: border-box; }
  .c-navbar__item:hover {
    border-bottom: 3px solid #48B0D3; }

.c-navbar__link {
  display: flex;
  flex-direction: row;
  font-weight: 700;
  color: #FFFFFF; }

.c-navbar__list {
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  height: 3.5rem; }

.c-navbar__logo {
  align-items: center;
  background-color: #48B0D3;
  border-radius: 50%;
  box-sizing: border-box;
  color: #FFFFFF;
  display: flex;
  font-weight: 700;
  height: 40px;
  justify-content: center;
  padding: 0.5rem;
  width: 40px; }

.c-button {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  border: 2px solid #48B0D3;
  padding: 8px 16px;
  width: 150px;
  height: 51px;
  color: #FFFFFF; }

.c-perfil__image {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 4px solid #48B0D3;
  height: 230px;
  width: 230px;
  border-radius: 50%; }

.c-about__me {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 600px; }

.c-cite {
  display: flex;
  width: 500px;
  max-width: 100%;
  align-items: center;
  padding: 10px; }

.c-card__icon {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  height: 75px;
  justify-content: center;
  padding: 6px;
  width: 75px; }

.c-card__project__image {
  align-self: center;
  box-sizing: border-box;
  max-height: 210px;
  height: auto;
  width: 100%; }

.c-card__project__link {
  height: 30px;
  width: 30px;
  margin: 4px; }
  .c-card__project__link--view {
    background: url("/src/assets/img/icon/eyes.svg") no-repeat center center;
    height: 30px;
    width: 30px; }
  .c-card__project__link--code {
    background: url("/src/assets/img/icon/code.svg") no-repeat center center;
    height: 30px;
    width: 30px; }

.c-card__project__show__description {
  background: url("/src/assets/img/icon/arrow.svg") center center no-repeat;
  box-sizing: border-box;
  height: 30px;
  left: 50%;
  padding: 6px;
  position: absolute;
  top: 0;
  transform: translateX(-50%);
  width: 30px; }
